export default {
name: "pagination",
  props : ['data', 'get_data', 'perpage'],
  data() {
    return {
      paginate_perpage: this.perpage.value,
    }
  },
  methods: {
    change_page(val) {
      this.get_data(val, () => {
        window.scrollTo({top: 0, behavior: 'smooth'});
      });
    }
  },
  watch: {
    paginate_perpage(val) {
      this.perpage.value = val;
      this.get_data(this.data.first_page_url, () => {
        window.scrollTo({top: 0, behavior: 'smooth'});
      });
    }
  },
}
